"use client"

import { Restaurant } from "@/redux/reduxTypes";
import axios from "axios";
import { useParams } from "next/navigation";
import { useEffect, useState } from "react"




export default function Page() {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const { id } = useParams() as { id: string };
  const [currentRestaurant, setCurrentRestaurant] = useState<Restaurant | null>(null);

  useEffect(() => {
    const getRestaurantInfo = async () => {
      try {
        setIsLoading(true);
        const res = await axios.get(`http://localhost:5200/api/restaurant/get-restaurant-by-id/${id}`);
        setCurrentRestaurant(res.data);
      } catch (err) {
        console.error(err);
      } finally {
        setIsLoading(false);
      }
    }
    getRestaurantInfo();
  }, [])

  return (
    <>
      <div className="relative w-full h-[600px]">
        <img
          src={currentRestaurant?.imageUrl}
          alt="restaurant photo"
          className="absolute inset-0 w-full h-full object-cover z-0"
        />
        <div className="absolute inset-0 custom-gradient z-10" />


        <div className="relative z-20 p-12 flex flex-col justify-end h-full basis-full gap-3">
          <h1 className="text-white text-5xl leading-12 font-semibold mb-1">{currentRestaurant?.title}</h1>
          <p className="text-2xl leading-8 text-[ #E8618CFF]">{currentRestaurant?.description}</p>
          <div className="flex gap-2 items-center">
            <div className="flex gap-2 items-center text-[#636AE8FF]">
              {calc}
            </div>
          </div>
        </div>
      </div>
    </>
  )



}
